.header {
    width: 100%;
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgb(119, 0, 46, 0.2);
}

.header ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: baseline;
}

.header li {
    margin-left: 3rem;
}

.header a {
    text-decoration: none;
    font-size: 1.5rem;
    color: rgb(119, 0, 46);
}

.header a:hover,
.header a:active,
.header a.active {
    color: white;
}